import React from "react";
import {
  Button,
  ModalCard,
  ModalRoot,
  ModalPage,
  Title,
  Div,
  FormItem,
  Input,
  Text,
} from "@vkontakte/vkui";
import { copy, redirectTo } from "./Notifications";
import toast from "react-hot-toast";
export function Modals({ activeModal, setActiveModal }) {
  const [qiwiSum, setQiwiSum] = React.useState(50.0);
  const qiwiNickname = "THED4N13L3K00";
  return (
    <ModalRoot activeModal={activeModal}>
      <ModalCard
        id="mainModal"
        onClose={() => setActiveModal(null)}
        header="Зачем?"
        subheader="Тут ничего нету"
        actions={
          <Div>
            <Button
              size="l"
              mode="commerce"
              onClick={() => setActiveModal(null)}
            >
              Ок
            </Button>
            <Button
              size="l"
              mode="commerce"
              onClick={() => setActiveModal(null)}
            >
              Не ок
            </Button>
          </Div>
        }
      />
      <ModalPage
        id="qiwi"
        onClose={() => setActiveModal(null)}
        dynamicContentHeight={true}
      >
        <Div>
          <Title
            style={{ width: "100%", textAlign: "center", marginTop: "0.5em" }}
            level="2"
            weight="semibold"
          >
            Qiwi
          </Title>
          <FormItem top="Сумма">
            <Input
              type="number"
              value={qiwiSum}
              onChange={(e) => {
                setQiwiSum(e.currentTarget.value);
              }}
              after={
                <Button
                  style={{ background: "#ff930c", color: "white" }}
                  size="l"
                  onClick={() => {
                    if (qiwiSum < 1) {
                      toast.error("Введите сумму не менее 1 рубля!");
                    } else {
                      let url = new URL("https://qiwi.com/payment/form/99999");
                      url.searchParams.append(
                        "extra['accountType']",
                        "nickname"
                      );
                      url.searchParams.append("extra['account']", qiwiNickname);
                      url.searchParams.append("currency", "643");
                      url.searchParams.append("blocked[0]", "sum");
                      url.searchParams.append("blocked[1]", "account");
                      url.searchParams.append(
                        "amountInteger",
                        parseInt(qiwiSum)
                      );
                      url.searchParams.append(
                        "amountFraction",
                        (qiwiSum % 1).toFixed(2) * 100
                      );
                      setActiveModal(null);
                      redirectTo(url);
                    }
                  }}
                >
                  Донат
                </Button>
              }
            />
          </FormItem>
          <Text
            style={{
              width: "100%",
              textAlign: "center",
              marginTop: "0.5em",
              marginBottom: "1em",
            }}
          >
            Просто введите сумму и нажмите на кнопку "Донат"
            <br />
            Вы будете перенаправлены на сайт Киви банка
          </Text>
          <Button
            style={{ background: "#ff930c", color: "white", width: "100%" }}
            size="l"
            data-copy={qiwiNickname}
            onClick={(e) => {
              copy(e);
              setActiveModal(null);
            }}
          >
            Мне просто нужен ник
          </Button>
        </Div>
      </ModalPage>
    </ModalRoot>
  );
}
