import React from "react";
import ReactDOM from "react-dom";
import {
  View,
  Cell,
  Epic,
  Panel,
  Group,
  VKCOM,
  Tabbar,
  AppRoot,
  SplitCol,
  ViewWidth,
  TabbarItem,
  SplitLayout,
  PanelHeader,
  usePlatform,
  withAdaptivity,
  ConfigProvider,
  AdaptivityProvider,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import anime from "animejs/lib/anime.es.js";
import { Toaster } from "react-hot-toast";
import {
  Icon28BriefcaseOutline,
  Icon28UserCircleOutline,
  Icon28InfoCircleOutline,
  Icon28MoneyTransferOutline,
  Icon28CommentOutline,
} from "@vkontakte/icons";
import { Me } from "./components/Me";
import { Modals } from "./components/Modals";
// import { About } from "./components/About";
import { Links } from "./components/Links";
import { Donate } from "./components/Donate";
import { Projects } from "./components/Projects";
import { CustomPreloader } from "react-preloaders2";
import ReactPlayer from "react-player";
import SnowStorm from "react-snowstorm";

const App = withAdaptivity(
  ({ viewWidth }) => {
    const platform = usePlatform();
    const [activeView, _setActiveView] = React.useState(
      window.location.hash ? window.location.hash.slice(1) : "me"
    );
    const [activeModal, setActiveModal] = React.useState(null);
    const [loader, setLoader] = React.useState(true);
    const isDesktop = viewWidth >= ViewWidth.TABLET;
    const hasHeader = platform !== VKCOM;
    const activeMenuStyle = {
      backgroundColor: "var(--button_secondary_background)",
      borderRadius: 8,
    };

    const setActiveView = (view) => {
      _setActiveView(view);
      window.location.hash = view;
    };

    const onStoryChange = (e) => {
      setActiveView(e.currentTarget.dataset.story);
      window.location.hash = e.currentTarget.dataset.story;
    };
    window.onhashchange = function () {
      setActiveView(
        window.location.hash ? window.location.hash.slice(1) : "me"
      );
    };

    window.addEventListener("load", () =>
      setTimeout(() => setLoader(false), 500)
    );
    React.useEffect(() => {
      fetch("https://ipwhois.app/json/")
        .then((r) => r.json())
        .then((r) => {
          if (r.latitude < 0) {
            anime({
              targets: "body",
              rotate: {
                value: 180,
                duration: 0,
              },
            });
          }
        });
    }, []);

    return (
      <SplitLayout
        header={hasHeader && <PanelHeader separator={false} />}
        style={{ justifyContent: "center" }}
        modal={
          <Modals activeModal={activeModal} setActiveModal={setActiveModal} />
        }
      >
        {[12, 1].includes(new Date().getMonth() + 1) && (
          <>{hasHeader && <SnowStorm animationInterval={30} />}</>
        )}

        {isDesktop && (
          <SplitCol fixed width="280px" maxWidth="280px">
            <Panel>
              {hasHeader && <PanelHeader>Brevzor</PanelHeader>}
              <Group>
                <Cell
                  disabled={activeView === "me"}
                  style={activeView === "me" ? activeMenuStyle : {}}
                  data-story="me"
                  onClick={onStoryChange}
                  before={<Icon28UserCircleOutline />}
                >
                  Кто я?
                </Cell>
                <Cell
                  disabled={activeView === "projects"}
                  style={activeView === "projects" ? activeMenuStyle : {}}
                  data-story="projects"
                  onClick={onStoryChange}
                  before={<Icon28BriefcaseOutline />}
                >
                  Карьера
                </Cell>
                <Cell
                  disabled={activeView === "links"}
                  style={activeView === "links" ? activeMenuStyle : {}}
                  data-story="links"
                  onClick={onStoryChange}
                  before={<Icon28CommentOutline />}
                >
                  Связь
                </Cell>
                <Cell
                  disabled={activeView === "donate"}
                  style={activeView === "donate" ? activeMenuStyle : {}}
                  data-story="donate"
                  onClick={onStoryChange}
                  before={<Icon28MoneyTransferOutline />}
                >
                  Донат
                </Cell>
                {/* <Cell
                  disabled={activeView === "about"}
                  style={activeView === "about" ? activeMenuStyle : {}}
                  data-story="about"
                  onClick={onStoryChange}
                  before={<Icon28InfoCircleOutline />}
                >
                  Что это?
                </Cell> */}
              </Group>
            </Panel>
          </SplitCol>
        )}

        <SplitCol
          animate={!isDesktop}
          spaced={isDesktop}
          width={isDesktop ? "560px" : "100%"}
          maxWidth={isDesktop ? "960px" : "100%"}
        >
          <Epic
            activeStory={activeView}
            tabbar={
              !isDesktop && (
                <Tabbar>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeView === "me"}
                    data-story="me"
                    text="Кто я?"
                  >
                    <Icon28UserCircleOutline />
                  </TabbarItem>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeView === "projects"}
                    data-story="projects"
                    text="Карьера"
                  >
                    <Icon28BriefcaseOutline />
                  </TabbarItem>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeView === "links"}
                    data-story="links"
                    text="Связь"
                  >
                    <Icon28CommentOutline />
                  </TabbarItem>
                  <TabbarItem
                    onClick={onStoryChange}
                    selected={activeView === "donate"}
                    data-story="donate"
                    text="Донат"
                  >
                    <Icon28MoneyTransferOutline />
                  </TabbarItem>
                  {/* <TabbarItem
                    onClick={onStoryChange}
                    selected={activeView === "about"}
                    data-story="about"
                    text="Что это?"
                  >
                    <Icon28InfoCircleOutline />
                  </TabbarItem> */}
                </Tabbar>
              )
            }
          >
            <View id="me" activePanel="me">
              <Panel id="me">
                <Me
                  setActiveView={setActiveView}
                  setActiveModal={setActiveModal}
                />
              </Panel>
            </View>
            <View id="links" activePanel="links">
              <Panel id="links">
                <Links
                  setActiveView={setActiveView}
                  setActiveModal={setActiveModal}
                />
              </Panel>
            </View>
            <View id="projects" activePanel="projects">
              <Panel id="projects">
                <Projects
                  setActiveView={setActiveView}
                  setActiveModal={setActiveModal}
                />
              </Panel>
            </View>
            <View id="donate" activePanel="donate">
              <Panel id="donate">
                <Donate
                  setActiveView={setActiveView}
                  setActiveModal={setActiveModal}
                />
              </Panel>
            </View>
            {/* <View id="about" activePanel="about">
              <Panel id="about">
                <About
                  setActiveView={setActiveView}
                  setActiveModal={setActiveModal}
                />
              </Panel>
            </View> */}
          </Epic>
        </SplitCol>
        <Toaster
          position="top-right"
          toastOptions={{
            style: {
              color: "whitesmoke",
              background: "#222",
            },
          }}
        />
        <CustomPreloader background="#19191A" customLoading={loader}>
          <ReactPlayer
            width="192px"
            url="/assets/videos/logo.webm"
            playing={true}
            loop={true}
            muted={true}
          />
        </CustomPreloader>
      </SplitLayout>
    );
  },
  {
    viewWidth: true,
  }
);

export default App;

ReactDOM.render(
  <ConfigProvider scheme="space_gray">
    <AdaptivityProvider>
      <AppRoot>
        <App />
      </AppRoot>
    </AdaptivityProvider>
  </ConfigProvider>,
  document.getElementById("root")
);
