import toast from 'react-hot-toast';

export function copy (e) {
    navigator.clipboard.writeText(e.currentTarget.dataset.copy);
    toast.success('Скопировано!');
}

export function copyOne (e) {
    navigator.clipboard.writeText(e);
    toast.success('Скопировано!');
}

export function redirectTo(link)  {
    const _ = toast.loading('Открываем внешнюю страницу...');
    setTimeout(() => {
        window.open(link, "_blank")
        toast.dismiss(_);
    }, 1000);
}

export function redirect(e)  {
    const href = e.currentTarget.dataset.href;
    const _ = toast.loading('Открываем внешнюю страницу...');
    setTimeout(() => {
        window.open(href, "_blank")
        toast.dismiss(_);
    }, 1000);
}
export function download(e)  {
    const href = e.currentTarget.dataset.href;
    const _ = toast.loading('Начинаем скачивание...');
    window.open(href, "_blank")
    setTimeout(() => {
        toast.dismiss(_);
    }, 2000);
}

export default (copy, copyOne, redirect, redirectTo, download);
