import React from "react";
import {
  Div,
  Text,
  Title,
  Group,
  Button,
  List,
  SimpleCell,
  PanelHeader,
} from "@vkontakte/vkui";
import { redirect } from "./Notifications";
import { BsTelegram, BsInstagram } from "react-icons/bs";
import { Icon20LogoVk, Icon16Link } from "@vkontakte/icons";

function Project({ title, link, job, work, instagram, telegram, vk }) {
  const styleOfTitle = { marginBottom: "4px" };
  const styleOfDesc = { marginBottom: "8px", whiteSpace: "normal" };
  const pre = (() => {
    if (work.toLowerCase().endsWith("now")) {
      work = work.slice(0, work.length - 3) + " года";
      return "ю с";
    } else {
      work += " гг.";
      return "л в";
    }
  })();
  return (
    <SimpleCell>
      <Title style={styleOfTitle} level="2" weight="semibold">
        {title}
      </Title>
      <Text style={styleOfDesc} type="regular">
        • Должность: {job}
        <br />• Работа{pre} {work}
      </Text>
      {link && (
        <Button
          onClick={redirect}
          data-href={link}
          style={{ marginRight: "4px" }}
          before={<Icon16Link />}
          mode="commerce"
        >
          Веб-сайт
        </Button>
      )}
      {telegram && (
        <Button
          onClick={redirect}
          data-href={telegram}
          mode="commerce"
          style={{
            verticalAlign: "top",
            marginRight: "4px",
            background: "#25A3E2",
          }}
          before={<BsTelegram />}
        >
          Telegram
        </Button>
      )}
      {vk && (
        <Button
          onClick={redirect}
          data-href={vk}
          mode="commerce"
          style={{
            verticalAlign: "top",
            marginRight: "4px",
            background: "#0077ff",
          }}
          before={<Icon20LogoVk />}
        >
          VK
        </Button>
      )}
      {instagram && (
        <Button
          onClick={redirect}
          data-href={instagram}
          mode="commerce"
          style={{
            verticalAlign: "top",
            marginRight: "4px",
            background:
              "linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%)",
          }}
          before={<BsInstagram />}
        >
          Instagram
        </Button>
      )}
    </SimpleCell>
  );
}

